@import url(https://fonts.googleapis.com/css?family=Raleway:400,500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

*{margin:0;padding:0}body{font-family:'Raleway', sans-serif;font-weight:400;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;height:100vh}.containter{text-align:center;width:100%;height:auto}#quote-box{margin-right:25vw;margin-left:25vw;font-weight:800;border-radius:5px;background-color:white}#text{padding-top:20px;margin:0 2vw 0 2vw;font-size:2em}#author{font-size:20px;text-align:right;margin-right:2vw;margin-top:10px}.button #new-quote{border-radius:3px;color:white;font-size:0.7em;padding:12px 15px 12px 15px;margin-left:20vw;opacity:1}.button #new-quote:hover{opacity:0.9}@media only screen and (max-width: 1050px){button#new-quote{margin-left:0 !important}#quote-box{margin-right:10vw;margin-left:10vw}}.button{height:50px;font-size:1.3em;margin-top:2vh;padding:1em;overflow:hidden}a{text-decoration:none;border-radius:10px;margin:0 1vw 0px 0px;padding:12px 25px 12px 25px;color:white;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;transition:all .5s}a:hover{-webkit-transform:translateY(-0.5rem);transform:translateY(-0.5rem);color:white}.fa{margin-right:0.5em;font-size:0.8em;text-align:inherit;-webkit-transform:translateY(-0.3rem);transform:translateY(-0.3rem)}

